import { animate } from './helpers';

const scroll = () => {
  const header = document.querySelector('.header');
  const headerSocial = document.querySelectorAll('.header__social-item');
  const headerMenu = document.querySelector('.header__menu');
  const sections = document.querySelectorAll('.main__section');

  const headerLinks = document.querySelectorAll('.header__menu-item');
  headerLinks.forEach(item => {
    item.addEventListener('click', (e) => {
      e.preventDefault()
      if (item = e.target) {
        let idAnchor = item.getAttribute('href')
        document.querySelector(idAnchor).scrollIntoView({
          block: 'start',
          behavior: 'smooth'
        });
      };
    });
  });

  /* Скролл по кнопке стрелки */
  const arrowUp = document.querySelector('.arrow-up');
  const logoHeader = document.querySelector('.header__content .logo');
  const arrayUp = [arrowUp, logoHeader]

  if (scrollY > 600) {
    arrowUp.style.opacity = '0.6';
    arrowUp.style.zIndex = '3';
  } else {
    arrowUp.style.opacity = '0';
    arrowUp.style.zIndex = '-1'; 
  }  

  window.addEventListener('scroll', () => {
    if (scrollY >= 600) {
      arrowUp.style.zIndex = '3';
      arrowUp.style.opacity = '0.6';
      arrowUp.style.transition = '0.6s opacity'
      arrowUp.style.display = 'block';
      headerMenu.style.top = '6rem'
      headerMenu.style.transition = '.6s all';
      header.style.height = '60px'
      header.style.transition = '0.6s all'
      sections.forEach(item => {
        item.style.padding = '6rem 0'
        item.style.transition = '.6s all';
      });
      headerSocial.forEach(item => {
        item.style.padding = '1rem 0'
        item.style.transition = '.6s all';
      });

    } else {
      arrowUp.style.opacity = '0'; 
      arrowUp.style.transition = '.6s all';
      arrowUp.style.zIndex = '-1';
      header.style.height = '80px'
      header.style.transition = '.6s all';
      headerMenu.style.top = '8rem'
      headerMenu.style.transition = '.6s all';
      sections.forEach(item => {
        item.style.padding = '8rem 0'
        item.style.transition = '.6s all';
      });
      headerSocial.forEach(item => {
        item.style.padding = '2rem 0'
        item.style.transition = '.6s all';
      });
    }   
  })

  arrayUp.forEach(item => {
    item.addEventListener('click', (e) => {
      e.preventDefault();
      animate({
        duration: 500,
        timing(timeFraction) {
          return timeFraction;
        },
        draw(progress) {
          window.scrollBy(0, -500 * progress)
        }
      });
    });
  });
};

export default scroll;
